<template lang="pug">
    div(class="page")
        Header
        section(class="contact")
            div(class="container")
                div(class="title")
                    h2 {{ $t("contact") }} 
                        span {{ $t("us") }} 
                div(class="contact-info")
                    div(class="row")
                        div(class="col-md-6 col-lg-6")
                            div(class="info-content wow fadeInUp" data-wow-duration="1s")
                                div(class='info-box')
                                    fa(class="theicon" :icon="['fa', 'map-marker-alt']")
                                    span {{ $t("address") }} 
                                    p {{ ContactData.address }}
                                div(class='info-box')
                                    fa(class="theicon" :icon="['fas', 'envelope']")
                                    span {{ $t("email") }} 
                                    p
                                        a(class="d-block" v-for="singleEmail in ContactData.email", :key="singleEmail.id" :href="'mailto:' + singleEmail") {{ singleEmail }}
                                div(class='info-box')
                                    fa(class="theicon" :icon="['fas', 'phone-alt']")
                                    span {{ $t("phone") }} 
                                    p
                                        a(:href="'tel:' + ContactData.phone") {{ ContactData.phone }}
                                div(class='info-box')
                                    fa(class="theicon" :icon="['fab', 'whatsapp']")
                                    span {{ $t("whatsApp") }}
                                    p 
                                        a(:href="'https://api.whatsapp.com/send?l=ar&amp;phone=' +ContactData.phone+'&amp;text=&amp;source=&amp;data='") {{ ContactData.whatsapp }}
                        div(class="col-md-6 col-lg-6")
                            .branchs 
                                .branch
                                    .icon 
                                        img(src="@/assets/images/icons/01.png", alt="")
                                    .text 
                                        h3 {{ $t("iraq-branch") }} 
                                        p
                                            a(:href="'tel:'+ ContactData.iraq_phone") {{ ContactData.iraq_phone }}
                                .branch
                                    .icon 
                                        img(src="@/assets/images/icons/02.png", alt="")
                                    .text 
                                        h3 {{ $t("kuwait-branch") }} 
                                        p
                                            a(:href="'tel:'+ ContactData.kuwait_phone") {{ ContactData.kuwait_phone }}
            div(class="contact-form")
                div(class="part")
                    div(class="container")
                        .map
                            div(class="subtitle")
                                h3 {{ $t("location") }}
                            iframe(:src="ContactData.map" style="border:0;" allowfullscreen="" loading="lazy")
                div(class="part")
                    div(class="container")
                        div(class='form-area')
                            div(class="subtitle")
                                h3 {{ $t("leave-message") }}
                            form(@submit="postFormData" ref="main_form" enctype="multipart/form-data" action='')
                                div(class='alert bg-danger alert-warning alert-dismissible fade show' role='alert' v-if="notSent")
                                    p {{ $t("error-message") }}
                                    //- button(type='button' class='close' data-dismiss='alert' aria-label='Close')
                                        span(aria-hidden='true') &times;
                                div(class='text-center' v-if="sent")
                                    p(class='successfully') {{ successMessage }}
                                .row
                                    div(class="col-lg-6")
                                        div(class='form-group')
                                            input(:placeholder="$t('first-name')" class='form-control' type='text' name="first_name" v-model="formData.first_name" required)
                                    div(class="col-lg-6")
                                        div(class='form-group')
                                            input(:placeholder="$t('last-name')" class='form-control' type='text' name="last_name" v-model="formData.last_name" )
                                    div(class="col-lg-12")            
                                        div(class='form-group')
                                            input(:placeholder="$t('email')" class='form-control' type='email' name="email" v-model="formData.email")
                                    div(class="col-lg-12")            
                                        div(class='form-group')
                                            input(:placeholder="$t('phone-number')" class='form-control' type='text' name="phone" v-model="formData.phone" required)
                                    div(class="col-lg-12")            
                                        div(class='form-group')
                                            textarea(rows='6' :placeholder="$t('message')" class='form-control' name="message" v-model="formData.message")
                                    div(class="col-lg-12")               
                                        div(class='form-group subm')
                                            button(class='btn btn-danger hvr-sweep-to-bottom' type='submit') {{ $t("send") }}
        Footer
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import { mapState } from "vuex";
// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

Vue.use(VueAxios, axios);

export default {
    name: "ContactPage",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.ContactData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.ContactData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.ContactData.meta_keywords,
                },
            ],
        };
    },
    components: {
        Header,
        Footer,
    },
    data() {
        return {
            errors: { name: "", phone: "" },
            successMessage: "",
            sent: false,
            notSent: false,
            formData: {
                first_name: "",
                last_name: "",
                email: "",
                phone: "",
                message: "",
            },
        };
    },
    methods: {
        postFormData(e) {
            e.preventDefault();
            // console.warn(this.formData);
            this.axios
                .post("contact-store", this.formData)
                .then((result) => {
                    // console.warn(result.data.data);
                    this.successMessage = result.data.data.message;
                    this.sent = true;
                    this.notSent = false;
                    this.$refs.main_form.reset();
                    this.formData.first_name = "";
                    this.formData.last_name = "";
                    this.formData.email = "";
                    this.formData.phone = "";
                    this.formData.message = "";
                })
                .catch((error) => {
                    this.notSent = true;
                    this.sent = false;
                    console.log(error);
                });

            // if (this.formData.first_name && this.formData.phone) return true;
            // this.errors = [];
            // if (!this.formData.first_name)
            //     this.errors.name = "name is required";
            // if (!this.formData.phone) this.errors.phone = "phone is required";
            // e.preventDefault();
        },
    },
    async created() {
        await this.$store.dispatch("getContactData");
    },
    computed: {
        ...mapState(["ContactData"]),
    },
};
</script>
